import React from "react";
import {connect} from "react-redux";
import ProductDetailsNavigationComponent from "eshop/modules/simfree/components/details/ProductDetailsNavigationComponent";
import ProductDetailsProductDescription from "eshop/modules/simfree/components/details/ProductDetailsProductDescription";
import ProductDetailsProductPromotion from "eshop/modules/simfree/components/details/ProductDetailsProductPromotion";
import ProductDetailsProductSpecification from "eshop/modules/simfree/components/details/ProductDetailsProductSpecification";
import ProductRecomendation from "eshop/modules/simfree/components/details/ProductRecomendation";
import ProductCartProductReviewComponent from "eshop/modules/simfree/components/details/ProductCartProductReviewComponent";
import {getSelectedDeviceTab} from "eshop/modules/simfree/selectors";
import {setSelectedDeviceTab} from "eshop/modules/simfree/actions/app";

class ProductDetailsNavigated extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this._loadModules();
        if(this.props.deviceData.setOf){
            let keys=Object.keys(this.props.deviceData.setOf);
            this.setDeviceTab(keys[0]);
        }
    }

    componentWillUnmount() {
        this.killModules();
    }

    _loadModules() {
        OPL.UI.loadModules(document.getElementById("opl-expander-product-details"), [{
            path: 'common/modules/opl-expander',
            options: {
                'scrollToSelected': true,
                'hideOtherElements': true
            },
            conditions: 'media:{(max-width:767px)}'
        }]);
        OPL.UI.initModules(document.getElementById("opl-expander-product-details-container"));
    }

    killModules() {
        OPL.UX.stopModules(document.getElementById("opl-expander-product-details-container"));
    }
    setDeviceTab(tab){
        this.props.setSelectedDeviceTab(tab);
    }

    render() {
        let deviceData=this.props.deviceData;
        console.log(deviceData.setOf );
        console.log(this.props.selectedDeviceTab);
        if(deviceData.setOf){
            if(this.props.selectedDeviceTab)
                deviceData=deviceData.setOf[this.props.selectedDeviceTab];
            else{
                let keys=Object.keys(deviceData.setOf);
                deviceData=deviceData.setOf[keys[0]];
            }
        }
        return (
            <div className="l-full-row">
               <SetElementTab deviceData={this.props.deviceData} selectedDeviceTab={this.props.selectedDeviceTab} onClickHandler={this.setDeviceTab.bind(this)}/>
               <ProductDetailsNavigationComponent deviceData={deviceData} noShadow={this.props.selectedDeviceTab?true:false} recommendedProducts={this.props.recomendedProducts}
                                                  recommendedProductsLabel={this.props.recommendedProductsNavigationLabel} promotionNavigationLabel={this.props.promotionNavigationLabel} />
                <div className="l-page-row u-small-padding-left-l u-small-padding-right-l" id={"opl-expander-product-details-container"}>
                    <div id={"opl-expander-product-details"} data-js-module={'common/modules/opl-expander'}
                         role={'tablist'} aria-multiselectable={false}>
                        <ProductDetailsProductPromotion promotion={deviceData.promotion} promotionLabel={this.props.promotionLabel}/>
                        <ProductDetailsProductDescription description={deviceData.description}/>
                        <ProductDetailsProductSpecification deviceData={deviceData}/>
                        <ProductRecomendation products={this.props.recomendedProducts} label={this.props.recommendedProductsLabel} productId={this.props.deviceData.productId}/>
                        <ProductCartProductReviewComponent deviceData={deviceData}/>
                    </div>
                </div>


            </div>
        )
    }
}

const SetElementTab =(props)=>{
    if(props.deviceData && !props.deviceData.setOf)
        return (null);
    let deviceData=props.deviceData;
    let selectedDevice=props.selectedDevice;
        let tabs=[];
        let contents=[];
        Object.keys(deviceData.setOf).forEach (function(deviceCode){
            let device=deviceData.setOf[deviceCode];
            tabs.push(
            <li onClick={()=>props.onClickHandler(device.productId)} key={"#set-item-"+device.name} className={"opl-tabs__nav-item"}>
                <div className="opl-tabs__nav-item__content">
                    <a href={"#tab_content-"+device.productId} className="opl-tabs__nav-link">
                        <span className="opl-tabs__nav-link-inner">
                            <span>{device.name}</span>
                        </span>
                    </a>
                </div>
            </li>);
           }

        );
return (
        <div className="js-stop-sticking u-padding-top-l">
<div className="l-page-row">

                <div className="opl-tabs defailt">
                  <div className="opl-tabs__nav-wrapper">
                    <div className="opl-tabs__nav-wrapper-inner">
                    <ul className="opl-tabs__nav js-tablist" data-js-module='common/modules/opl-tabs'>
                        {tabs}
                    </ul>
                    </div>
                    </div>


                    </div>
</div>

        </div>
        )

}

const mapStateToProps = state => ({
    selectedDeviceTab: getSelectedDeviceTab(state)
});
const mapDispatchToProps = (dispatch, props) => ({
    setSelectedDeviceTab: (selectedDeviceTab) => dispatch(setSelectedDeviceTab(selectedDeviceTab))
});

export default  connect(mapStateToProps, mapDispatchToProps)(ProductDetailsNavigated);